  --titles-color: #d41443;
  --light-grey: #907e82;
  --sand-yellow: #f0d1a5;
  --light-green: #c2e297;
  --border-line-color: #e1e1e1;
  --about-bg-color: #fff5f6;
  --contacts-bg-color: #fff5f6;
  --reviews-bg-color: #fafafa;
  --pagination-dot: #d9d9d9;
  --pagination-dot-red: #c7133e;
  --menu-secon-card: orange;
  --button-red-hover: #d41342;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  background-color: var(--light-pink);
}

.header-nav {
  justify-content: space-between;
  align-items: center;
  padding: 8px 0 0;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header-nav {
    padding: 32px 0 0;
  }
}

@media screen and (min-width: 1200px) {
  .header-nav {
    padding: 37px 0 0;
  }
}

.header-button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .header-button {
    display: none;
  }
}

.button-buy {
  width: 160px;
  height: 44px;
  background-color: var(--main-white);
  color: var(--red-color);
  cursor: pointer;
  border: none;
  border-radius: 22px;
  padding: 0 30px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  transition: background-color .25s, transform .25s;
  animation: glowing 1.5s infinite;
  display: none;
}

.button-buy:hover, .button-buy:focus {
  background-color: #ecebeb;
  transform: scale(1.05);
}

@media screen and (min-width: 768px) {
  .button-buy {
    justify-content: space-around;
    align-items: center;
    margin: 0 0 0 32px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .button-buy {
    margin: 0 0 0 141px;
  }
}

@keyframes glowing {
  0% {
    transform: scale(1);
    box-shadow: 0 0 5px #d41443;
  }

  50% {
    transform: scale(1.05);
    box-shadow: 0 0 20px #ffb8ca;
  }

  100% {
    transform: scale(1);
    box-shadow: 0 0 5px #d41443;
  }
}

@media screen and (min-width: 768px) {
  @keyframes glowing {
    0% {
      transform: scale(1);
      box-shadow: 0 0 5px #ffb8ca;
    }

    50% {
      transform: scale(1.05);
      box-shadow: 0 0 20px #d41443;
    }

    100% {
      transform: scale(1);
      box-shadow: 0 0 5px #ffb8ca;
    }
  }
}

.site-nav {
  display: none;
}

@media screen and (min-width: 1200px) {
  .site-nav {
    z-index: 3;
    align-items: center;
    transition: transform .25s;
    display: flex;
    position: relative;
  }
}

.site-nav-item:not(:last-child) {
  margin-right: 35px;
}

.site-nav-item:hover {
  transform: scale(1.05);
}

.site-nav-link {
  color: var(--main-white);
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  transition: color .25s;
}

.site-nav-link:hover, .site-nav-link:focus {
  color: var(--red-color);
  mask-image: linear-gradient(-75deg, rgba(0, 0, 0, .6) 30%, #000 50%, rgba(0, 0, 0, .6) 70%);
  -webkit-mask-size: 200%;
  mask-size: 200%;
  animation: wave 2s infinite;
  -webkit-mask-image: -webkit-linear-gradient(-75deg, rgba(0, 0, 0, .6) 30%, #000 50%, rgba(0, 0, 0, .6) 70%);
}

@keyframes wave {
  from {
    -webkit-mask-position: 150%;
  }

  to {
    -webkit-mask-position: -50%;
  }
}

